import React, { Component } from 'react';
import './Game.css';
import CardView, { LIBRARIES } from './CardView';
import MemoryCards from './MemoryCards';

// game mode progresses from 1. showing all icons for a
// few seconds, to 2. waiting for the user's first click,
// to 3. user is playing
const GAME_MODES = Object.freeze({
  showingAllIcons: "showingAllIcons",
  waitingForFirstClick: "waitingForFirstClick",
  userIsPlaying: "userIsPlaying"
});

class Game extends Component {
  constructor(props) {
    super(props);
    this.onCardClicked = this.onCardClicked.bind(this);
    this.onPlayAgain = this.onPlayAgain.bind(this);
    this.memoryCards = new MemoryCards();
    // milliseconds after which a wrong pair will be flipped
    this.wrongPairClearTimeout = 1000;

    this.displayAllCardsTimeout = 3000;

    switch (props.match.params['library']) {
    case LIBRARIES.jazzicon.toLowerCase():
      this.library = LIBRARIES.jazzicon;
      break;
    case LIBRARIES.identicon.toLowerCase():
      this.library = LIBRARIES.identicon;
      break;
    default:
      this.library = LIBRARIES.blockies;
    }
  }

  componentWillMount() {
    this.initGame();
  }

  initGame() {
    this.memoryCards.generateCardSet();
    this.setState({
      turnNo : 1,
      pairsFound : 0,
      numClicksWithinTurn : 0,
      firstId : undefined,
      secondId : undefined,
      currentGameMode: GAME_MODES.showingAllIcons
    });

    // showing the cards for a couple of seconsd
    this.memoryCards.flipAllCards(true);
    setTimeout(() => {
      this.memoryCards.flipAllCards(false);
      this.setState({
        currentGameMode: GAME_MODES.waitingForFirstClick
      });
    }, this.displayAllCardsTimeout);
  }

  getCardViews() {
    let cardViews = [];
    let onClick = this.onCardClicked;
    this.memoryCards.cards.forEach(c => {
      let cardView = <CardView key={c.id}
                               id={c.id}
                               value={c.value}
                               imageUp = {c.imageUp}
                               matched = {c.matched} 
                               onClick={onClick}
                               library={this.library}/>;
      cardViews.push(cardView);
    });
    return cardViews;
  }

  clearCards(id1,id2) {
    if (this.state.numClicksWithinTurn !== 2) {
      return;
    }
    this.memoryCards.flipCard(this.state.firstId, false);
    this.memoryCards.flipCard(this.state.secondId, false);
    this.setState({
      firstId: undefined,
      secondId: undefined,
      numClicksWithinTurn: 0,
      turnNo : this.state.turnNo+1
    });
  }

  hasInitTimeBeenSet() {
    return !!this.state.initTime;
  }

  onCardClicked(id,image) {
    if (this.state.currentGameMode === GAME_MODES.showingAllIcons) {
      // do nothing, user should wait
      return;
    }
    
    if (this.state.currentGameMode === GAME_MODES.waitingForFirstClick) {
      this.setState({
        initTime: new Date(),
        currentGameMode: GAME_MODES.userIsPlaying
      });
    }
    
    if (this.state.numClicksWithinTurn === 0 || this.state.numClicksWithinTurn === 2) {
      if (this.state.numClicksWithinTurn === 2) {
        clearTimeout(this.timeout);
        this.clearCards(this.state.firstId, this.state.secondId);        
      }
      this.memoryCards.flipCard(id, true);
      this.setState({
        firstId : id,
        numClicksWithinTurn : 1
      });
    } else if (this.state.numClicksWithinTurn === 1) {
      this.memoryCards.flipCard(id, true);
      this.setState({
        secondId : id,
        numClicksWithinTurn : 2
      });

      if (this.memoryCards.cardsHaveIdenticalValues(id, this.state.firstId)) {
        this.memoryCards.setCardAsMatched(this.state.firstId, true);
        this.memoryCards.setCardAsMatched(id, true);
        this.setState({
          pairsFound: this.state.pairsFound+1,
          firstId: undefined,
          secondId: undefined,
          turnNo : this.state.turnNo+1,
          numClicksWithinTurn: 0
        });

      } else {
        this.timeout = setTimeout(() => { 
          this.clearCards(this.state.firstId, this.state.secondId);
        }, this.wrongPairClearTimeout); 
      }

    }
  }

  onPlayAgain() {
    this.initGame();
  }

  render() {
    let cardViews = this.getCardViews();
    let gameStatus = <div className='Game-status'>
                      <div>Turn: {this.state.turnNo}</div>
                      <div>Pairs found: {this.state.pairsFound}</div>
                    </div>;

    if (this.state.pairsFound === this.memoryCards.NUM_IMAGES) {
      let gameDuration = Math.round(((new Date()).getTime() - this.state.initTime.getTime()) / 1000);
      gameStatus = <div className='Game-status'>
                     <div>GAME COMPLETE!</div>
                     <div>You used {this.state.turnNo-1} turns</div>
                     <div>Time: {gameDuration}s</div>
                     <div><button onClick={this.onPlayAgain}>Play again?</button></div></div>;
    }

    return (
      <div className='Game'>
        <header className='Game-header'>
          <div className='Game-title'>{this.library} Benchmarker // Memory Game</div>
        </header>
        <div>
          {gameStatus}
        </div>
        <div className='CardContainer'>
          {cardViews}
        </div>
      </div>
    );
  }
}

export default Game;



