// modified from https://github.com/ethereum/blockies/blob/master/react-component.js

import React from 'react';
import * as Blockies from 'ethereum-blockies/blockies';

/*
  Allowed props:
  - seed (required)
  - color
  - bgcolor
  - size
  - scale
  - spotcolor
*/
class ReactBlockie extends React.Component {
  
  componentDidMount() {
    Blockies.render(this.props, this.canvas);
  }
  
  render() {
    return React.createElement("canvas", {ref: canvas => this.canvas = canvas});
  }
}

export default ReactBlockie;



