import React from 'react';
import * as Identicon from 'identicon.js/identicon.js';

class ReactIdenticon extends React.Component {
  constructor(props) {
    super(props);

    const data = new Identicon(this.props.seed.slice(-32), this.props.size).toString();
    this.src = `data:image/png;base64,${data}`;
  }
  
  render() {
    return <img width={this.props.size} height={this.props.size} src={this.src} alt='Identicon'/>;
  }
}

export default ReactIdenticon;



