import shuffle from 'shuffle-array';

class MemoryCards {
  constructor() {
    this.cards = [];
    this.NUM_IMAGES = 8;
  }

  // pads a numeric string with zeros to the left
  // e.g. when string='43' and n=4, it returns '0043'
  padWithZeros(string, n) {
    return ('0' * n + string).slice(-n); 
  }

  // generates ethereum address
  generateRandomAddress() {
    var result = '0x';
    // composing the 20-byte address
    for (var i = 0; i < 4; i++) {
      const randomNumber = Math.floor(Math.random() * Math.pow(16,10));
      const stringNumber = randomNumber.toString(16);
      result += this.padWithZeros(stringNumber, 10);
    }
    return result;
  }

  generateCardSet() {
    //
    // Generate a set of cards with image pairs
    //
    this.cards = [];
    for(let i=1; i <= this.NUM_IMAGES; i++) {
    // taken from http://download13.github.io/blockies/ source code
      var value = this.generateRandomAddress();
    
      let card1 = {
        id: `${this.cards.length}-${value}`,
        value: value,
        imageUp: false,
        matched: false
      };
      this.cards.push(card1);
      let card2 = {
        id: `${this.cards.length}-${value}`,
        value: value,
        imageUp: false,
        matched: false
      };
      this.cards.push(card2);
    }

    // Randomize the card set
    shuffle(this.cards);  
  }

  getCard(id) {
    for(let i=0; i < 2*this.NUM_IMAGES; i++) {
      if (this.cards[i].id === id) {
        return this.cards[i];
      }
    };
    // should never be called
    return null;
  }

  flipCard(id, imageUp) {
    this.getCard(id).imageUp = imageUp;
  }

  flipAllCards(imageUp) {
    this.cards.forEach(c => { c.imageUp = imageUp; });
  }

  setCardAsMatched(id, matched) {
    this.getCard(id).matched = matched;
  }

  cardsHaveIdenticalValues(id1, id2) {
    if (this.getCard(id1).value === this.getCard(id2).value) {
      return true;
    } else {
      return false;
    }
  }

};

export default MemoryCards;



