import React, { Component } from 'react';
import './Game.css';
import ReactBlockie from './ReactBlockie';
import ReactJazzicon from './ReactJazzicon';
import ReactIdenticon from './ReactIdenticon';

const LIBRARIES = Object.freeze({
  blockies: 'Blockies',
  jazzicon: 'Jazzicon',
  identicon: 'Identicon'
});

class CardView extends Component {
  constructor(props) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    if (!this.props.matched && !this.props.imageUp) {
      this.props.onClick(this.props.id,this.props.image);      
    }
  }

  imageView() {
    switch (this.props.library) {
      
    case LIBRARIES.blockies:
      return <ReactBlockie seed={this.props.value} size={10} scale={10} onClick={this.onClick}/>;
      
    case LIBRARIES.jazzicon:
      return <ReactJazzicon seed={this.props.value} diameter={100} onClick={this.onClick}/>;
      
    case LIBRARIES.identicon:
      return <ReactIdenticon seed={this.props.value} size={100} onClick={this.onClick}/>;
      
    default:
      return null;
    }
  }

  render() {
    let className='Card';
    if (this.props.matched) {
      className = className + ' Matched';
    }

    var contents;
    
    if (this.props.imageUp) {
      contents = this.imageView();
    } else {
      let imPath = './images/back.jpg';
      contents = (
        <img src={require(`${imPath}`)} alt='' onClick={this.onClick}/>
      );
    }

    return (
        <div className={className}>{contents}</div>
    );
  };
};

export default CardView;
export { LIBRARIES };



