// modified from https://github.com/ethereum/blockies/blob/master/react-component.js

import React from 'react';
import * as Jazzicon from 'jazzicon';
import Parser from 'html-react-parser';

class ReactJazzicon extends React.Component {
  constructor(props) {
    super(props);

    this.jsNumberAddress = this.jsNumberForAddress(this.props.seed);
  }
  
  // as the jazzicon library accepts only JS integers,
  // we need to convert the 20-byte ethereum address to
  // an much smaller integer that JS can handle. Metamask
  // takes the first 8 digits of the addess (not including '0x').
  // taken from https://github.com/MetaMask/metamask-extension/blob/develop/ui/lib/icon-factory.js#L60
  jsNumberForAddress(address) {
    const addr = address.slice(2, 10);
    return parseInt(addr, 16);
  }
  
  render() {
    const icon = Jazzicon(this.props.diameter, this.jsNumberAddress);
    return (
      // using the HTML parser here because React doesn't like
      // HTMLElement objects created outside react. So we're
      // converting it to an HTML string and we parse it in React
      <div>
        {Parser(icon.outerHTML)}
      </div>
    );
  }
}

export default ReactJazzicon;



