import React, { Component } from 'react';
import Game from './Game';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

class App extends Component {

  render() {
    return (
      // router is used to capture the query params in Game
      <BrowserRouter basename='/memorygame'>
        <Switch>
          <Route path='/:library' component={Game}/>
          {/* still go to Game even if no lib is passed */}
          <Route path='/' component={Game}/>
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;



